<template>
	<div id="order-detail">
		<public-header title="订单详情"></public-header>
		<div class="content">
			<div class="order-status bg-default margin-10 padding-15-t text-center">
				<div>
					<span class="font-18 font-bold" @click="onTogglePopup">订单{{order.status_cn}}</span><i class="icon icon-right c-gray"></i>
				</div>
				<div class="c-gray margin-15-tb font-14" v-if="order.log && order.delivery_status != 7">{{order.log.note}}</div>
				<div class="font-15 margin-15-t margin-10-b" v-if="order.status >= 2 && order.status < 5 && order.order_type == 1">
					<i class="icon icon-timefill fobt-16"></i>
					<span class="c-gray">配送时间:</span>
					<span class="c-info">{{order.delivery_time}}</span>
				</div>
				<div class="order-btn margin-10-b margin-15-t" v-if="order.status < 5">
					<template v-if="order.status == 1">
						<div class="btn bg-info" @click="onUpdateOrderStatus('handle')">确认</div>
					</template>
					<template v-else-if="order.status == 2 || order.status == 3">
						<template v-if="order.order_type == 1">
							<div class="btn bg-info" @click="onUpdateOrderStatus('notify_deliveryer_collect')">通知配送</div>
							<template v-if="order.store && order.store.delivery_mode == 1">
								<div class="btn bg-info" @click="onUpdateOrderStatus('direct_deliveryer')">指定配送</div>
								<div class="btn bg-info" @click="onUpdateOrderStatus('delivery_ing')">设为配送中</div>
								<div class="btn bg-info" @click="util.jspost({url: 'manage/order/takeout/push_dada', data: {id: id}, confirm: '确定推送到达达配送吗?'})" v-if="order.store.data.dada && order.store.data.dada.status == 1">达达配送</div>
							</template>
							<div class="btn bg-info" @click="onPushUU(order.id)" v-if="order.store.data.uupaotui && order.store.data.uupaotui.status == 1">UU配送</div>
						</template>
						<template v-else-if="order.order_type == 2">
							<div class="btn bg-info" @click="onUpdateOrderStatus('end')">已自提</div>
						</template>
						<template v-else-if="order.order_type >= 3">
							<div class="btn bg-info" @click="onUpdateTanshiOrderStatus('end')">完成</div>
						</template>
					</template>
					<template v-else-if="order.status == 4">
						<div class="btn bg-info" v-if="order.order_type != 1" @click="onUpdateOrderStatus('end')">完成</div>
					</template>
					<template v-else-if="order.delivery_type == 1 && order.status > 1 && order.status < 5 && order.order_type == 1">
						<div class="btn bg-info" @click="onUpdateOrderStatus('end')">完成</div>
					</template>
					<div class="btn bg-info" @click="onUpdateOrderStatus('reply')" v-if="order.is_remind == 1">回复</div>
					<template v-if="order.order_type > 2">
						<div class="btn bg-danger" @click="onUpdateTanshiOrderStatus('cancel_refund')" v-if="order.is_pay == 1 && order.pay_type != 'delivery'">取消并退款</div>
						<div class="btn bg-danger" @click="onUpdateTanshiOrderStatus('cancel')" v-else>取消</div>
					</template>
					<template v-else>
						<div class="btn bg-danger" @click="onUpdateOrderStatus('cancel')" v-if="order.status == 1 || (order.status > 1 && config.cancel_after_handle == 1)">取消</div>
					</template>
				</div>
				<template v-if="order.deliveryer_id > 0">
					<div class="divide">
						<div class="divide-line"></div>
					</div>
					<div class="padding-15-lr padding-10-tb flex-lr">
						<div class="flex ">
							<div class="deliveryer-avatar">
								<img class="wh-100" :src="order.deliveryer.avatar" alt="">
							</div>
							<div class="font-14">
								<div class="flex">
									<span class="margin-5-r">{{order.deliveryer.title}}</span>
									<div class="allocation-icon">{{order.delivery_title}}</div>
								</div>
								<div class="c-gray margin-10-t">{{order.deliveryer.mobile}}</div>
							</div>
						</div>
						<div class="flex">
							<div class="icon icon-telephone icon-telephone1" @click="util.jsTel(order.deliveryer.mobile)"></div>
							<div class="icon icon-zuobiao font-16 margin-10-l" v-if="order.deliveryer.location_x && order.deliveryer.location_y" @click="util.jsLocation({lat: order.deliveryer.location_x, lng: order.deliveryer.location_y})"></div>
						</div>
					</div>
				</template>
			</div>
			<div class="shop-content margin-10 bg-default">
				<div class="padding-15-lr padding-10-tb flex-lr">
					<div class="flex" v-if="order.store">
						<div class="store-logo">
							<img class="wh-100" :src="order.store.logo" alt="">
						</div>
						<span class="c-gray font-14 margin-5-l">{{order.store.title}}</span>
					</div>
				</div>
				<div class="goods-list" v-if="order.goods && order.goods.length > 0">
					<div class="goods-item bg-gray padding-15-lr padding-10-tb flex-lr" v-for="item in order.goods">
						<div class="goods-thumb">
							<img :src="item.thumb" alt="" class="wh-100">
						</div>
						<div class="goods-info font-14">
							<div class="flex-lr margin-10-b">
								<div>{{item.goods_title}}</div>
								<div>¥{{item.goods_price}}</div>
							</div>
							<div class="flex-lr c-gray font-12">
								<div>x{{item.goods_price}}</div>
								<div class="line-through">¥{{item.goods_original_price}}</div>
							</div>
						</div>
					</div>
				</div>
				<div class="other-info padding-15-lr font-14">
					<div class="flex-lr margin-10-tb">
						<span>配送费</span>
						<span>¥{{order.delivery_fee}}</span>
					</div>
					<div class="flex-lr margin-10-tb">
						<span>餐盒费</span>
						<span>¥{{order.box_price}}</span>
					</div>
					<div class="flex-lr margin-10-tb">
						<span>包装费</span>
						<span>¥{{order.pack_fee}}</span>
					</div>
					<div class="flex-lr margin-10-tb">
						<span>平台抽取佣金</span>
						<span>-¥{{order.plateform_serve_fee}}</span>
					</div>
					<div class="flex-lr margin-10-tb">
						<span>平台配送费</span>
						<span>-¥{{order.plateform_delivery_fee}}</span>
					</div>
					<div class="flex-lr margin-10-tb">
						<span>商户优惠活动</span>
						<span>-¥{{order.store_discount_fee}}</span>
					</div>
					<div class="flex-lr margin-10-tb">
						<span>平台补贴</span>
						<span>+¥{{order.plateform_discount_fee}}</span>
					</div>
				</div>
				<template v-if="order.activityed && order.activityed.length > 0">
					<div class="divide">
						<div class="divide-line"></div>
					</div>
					<div class="discount-box padding-15-lr" v-for="item in order.activityed">
						<div class="discount-item flex-lr">
							<div class="flex font-14">
								<div class="discount-img">
									<img class="wh-100" :src="'static/img/' + item.type + '_b.png'" alt="">
								</div>
								<div>{{item.name}}</div>
							</div>
							<div class="c-danger">{{item.note}}</div>
						</div>
					</div>
				</template>
				<div class="divide">
					<div class="divide-line"></div>
				</div>
				<div class="padding-15-lr padding-10-tb text-right font-14">
					<span class="c-gray">
						<span>已优惠¥{{order.discount_fee}}</span>
						<span class="padding-10-lr">顾客支付¥{{order.final_fee}}</span>
						<span class="c-default">预计收入</span>
						<span class="c-danger font-bold">¥{{order.store_final_fee}}</span>
					</span>
				</div>
				<div class="divide">
					<div class="divide-line"></div>
				</div>
				<div class="padding-15-lr padding-10-t padding-15-b text-center" @click="util.jsTel(order.mobile)">
					<i class="icon icon-telephone"></i>
					<span>联系顾客</span>
				</div>
			</div>
			<div class="bg-default margin-10 padding-15 font-14" v-if="order.order_type == 2 || order.order_type == 1">
				<van-row>
					<van-col span="7" class="c-gray" v-if="order.order_type == 1">配送时间</van-col>
					<van-col span="7" class="c-gray" v-else-if="order.order_type == 2">自提时间</van-col>
					<van-col span="17">{{order.delivery_day}} {{order.delivery_time}}</van-col>
				</van-row>
				<van-row class="margin-10-tb">
					<van-col span="7" class="c-gray">{{order.order_type == 1 ? '配送' : '自提'}}地址</van-col>
					<van-col span="17" v-if="order.order_type == 1">
						<div class="margin-10-b">{{order.username}}({{order.sex}}){{order.mobile}}</div>
						<div class="line-12">{{order.address}}</div>
					</van-col>
					<van-col span="17" v-else>
						<div class="margin-10-b">{{order.store.title}}</div>
						<div class="line-12">{{order.store.address}}</div>
					</van-col>
				</van-row>
				<van-row v-if="order.order_type == 1">
					<van-col span="7" class="c-gray">配送服务</van-col>
					<van-col span="17">
						<div class="allocation-icon" v-if="order.delivery_type == 2">{{order.delivery_title}}</div>
						<span>{{order.delivery_type == 1 ? '由 商家' : ''}}提供高品质配送服务</span>
					</van-col>
				</van-row>
			</div>
			<div class="bg-default margin-10 padding-15 font-14">
				<template v-if="order.order_type <= 2">
					<van-row class="flex-lr">
						<van-col span="7" class="c-gray">商户订单号</van-col>
						<van-col
							v-clipboard:copy="order.serial_sn"
							v-clipboard:success="onCopy" 
							span="17" 
							class="flex-lr"
						>
							<span>#{{order.serial_sn}}</span>
							<span class="copy">复制</span>
						</van-col>
					</van-row>
					<van-row class="margin-10-tb">
						<van-col span="7" class="c-gray">平台订单号</van-col>
						<van-col span="17">{{order.ordersn}}</van-col>
					</van-row>
					<van-row class="margin-10-tb">
						<van-col span="7" class="c-gray">收货码</van-col>
						<van-col span="17">{{order.code}}</van-col>
					</van-row>
				</template>
				<van-row class="margin-10-tb" v-if="order.order_type == 3">
					<van-col span="7" class="c-gray">桌台号</van-col>
					<van-col span="17">{{order.table.title}}号桌</van-col>
				</van-row>
				<template v-if="order.order_type == 4">
					<van-row class="margin-10-tb">
						<van-col span="7" class="c-gray">预定人</van-col>
						<van-col span="17">
							<span>{{order.username}}</span>
						</van-col>
					</van-row>
					<van-row class="margin-10-tb">
						<van-col span="7" class="c-gray">预定时间</van-col>
						<van-col span="17">{{order.reserve_time}}</van-col>
					</van-row>
					<van-row class="margin-10-tb">
						<van-col span="7" class="c-gray">预定桌台</van-col>
						<van-col span="17">{{order.table_category.title}}</van-col>
					</van-row>
					<van-row class="margin-10-tb">
						<van-col span="7" class="c-gray">预定类型</van-col>
						<van-col span="17">{{order.reserve_type_cn}}</van-col>
					</van-row>
				</template>
				<van-row class="margin-10-tb" v-if="order.order_type == 1">
					<van-col span="7" class="c-gray">配送时间</van-col>
					<van-col span="17">
						{{order.delivery_day}} {{order.delivery_time}}
					</van-col>
				</van-row>
				<van-row>
					<van-col span="7" class="c-gray">支付方式</van-col>
					<van-col span="17">{{order.pay_type_cn}}</van-col>
				</van-row>
				<template v-if="order.invoice">
					<van-row>
						<van-col span="7" class="c-gray" v-if="order.invoice.title">发票抬头</van-col>
						<van-col span="17">{{order.invoice.title}}</van-col>
					</van-row>
					<van-row>
						<van-col span="7" class="c-gray" v-if="order.invoice.recognition">纳税人识别号</van-col>
						<van-col span="17">{{order.invoice.recognition}}</van-col>
					</van-row>
				</template>
				<van-row>
					<van-col span="7" class="c-gray" v-if="order.note">纳税人识别号</van-col>
					<van-col span="17">{{order.note}}</van-col>
				</van-row>
			</div>
		</div>
		<van-popup v-model="popupStatus" position="bottom">
			<div class="popup-title">
				<span class="opacity-0 font-14">取消</span>
				<span>订单跟踪</span>
				<span class="c-info font-14" @click="onTogglePopup">关闭</span>
			</div>
			<div class="popup-content">
				<van-steps direction="vertical" :active="order.logs_length - 1" active-color="#44BB00">
					<van-step class="border-0px" v-for="(item, index) in order.logs" :key="index">
						<div class="flex-lr">
							<span>{{item.title}}</span>
							<span>{{item.addtime_cn}}</span>
						</div>
						<div class="margin-5-t">{{item.note}}</div>
					</van-step>
				</van-steps>
			</div>
		</van-popup>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData, setData} from "@/controller/funcCommon"
	import {changeOrderStatus, changeTangshiOrderStatus} from "./controller/order.js"
	export default {
		components: {
			publicHeader,
		},
		data() {
			return {
				showPreLoading: true,
				isRefresh: false,
				popupStatus: false,
				id: 0,
				order: {},
				config: {}
			}
		},
		methods: {
			onTogglePopup() {
				this.popupStatus = !this.popupStatus;
			},
			onLoad() {
				var that = this;
				getData({
					vue: that,
					url: 'manage/order/takeout/detail',
					data: {id: that.id},
					success: function(res) {
						that.order = res.order;
						that.config = res.config;
						that.isRefresh = false;
						var logs_length = 0;
						if(that.order.logs) {
							for(var i in that.order.logs) {
								logs_length++;
							}
						}
						that.order.logs_length = logs_length;
					}
				})
			},
			onUpdateOrderStatus(type) {
				var that = this;
				changeOrderStatus({
					vue: that,
					type: type,
					order: that.order,
					from: 'detail'
				})
			},
			onUpdateTanshiOrderStatus(type) {
				var that = this;
				changeTangshiOrderStatus({
					vue: that,
					type: type,
					order: that.order,
					from: 'detail'
				})
			},
			onPushUU(orderId) {
				if(!orderId) {
					return false;
				}
				var that = this;
				setData({
					vue: that,
					url: 'manage/order/takeout/push_uupaotui',
					data: {
						id: orderId
					},
					success: function(res) {
						setData({
							vue: that,
							url: 'manage/order/takeout/push_uupaotui',
							confirm: res.tips,
							data: {
								id: res.id,
								push: 1
							},
						});
					},
				});
			},
			onCopy(e) {
				this.util.$toast('复制成功');
			},
		},
		mounted() {
			this.onLoad();
		},
		created() {
			this.query = this.$route.query;
			if(this.query && this.query.id) {
				this.id = this.query.id;
			}
		},
	}
</script>

<style>
	#order-detail .order-status{
		border-bottom: 1px solid #fff;
	}
	#order-detail .order-status .order-btn{
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: center;
	}
	#order-detail .order-status .order-btn .btn{
		font-size: 14px;
		padding: 8px 10px;
		border: 1px solid #ddd;
		border-radius:3px;
		margin: 0 5px;
	}
	#order-detail .allocation-icon{
		display:inline-block;
		position:relative;
		background-color:#33aafc;
		height:15px;
		line-height:15px;
		padding:0 6px;
		font-size:10px;
		color:#fff;
		font-weight:normal;
		vertical-align:middle;
		border-radius:6px 0;
	}
	#order-detail .order-status .icon-zuobiao{
		width: 40px;
		height: 40px;
		line-height: 40px;
		border: 1px solid #e7e7e7;
		border-radius: 100%;
		background-color: #fff;
		color: #656565;
		text-align: center;
	}
	#order-detail .order-status .deliveryer-avatar{
		width: 50px;
		height: 50px;
		border-radius: 50%;
		overflow: hidden;
		margin-right: 10px;
	}
	#order-detail .shop-content .store-logo{
		width: 30px;
		height: 30px;
		border-radius: 50%;
		overflow: hidden;
	}
	#order-detail .shop-content .goods-item{
		background-color: #f8f8f8;
		margin-bottom: 10px;
	}
	#order-detail .shop-content .goods-item:last-child{
		margin-bottom: 0;
	}
	#order-detail .shop-content .goods-item .goods-thumb{
		width: 55px;
		height: 55px;
	}
	#order-detail .shop-content .goods-item .goods-info{
		height: 55px;
		flex: 1;
		padding-left: 10px;
	}
	#order-detail .shop-content .discount-img{
		width: 17px;
		height: 17px;
		margin-right: 5px;
	}
	#order-detail .shop-content .discount-item{
		margin: 10px 0;
	}
	#order-detail .copy{
		padding: 5px 10px;
		border: 1px solid #ddd;
		border-radius: 3px;
		font-size: 12px;
	}
</style>


